@import '../../variables.scss';

.start {
  .question-types,
  .question-settings,
  .question-ctrl {
    margin: $margin-md;
  }

  .question-types-item {
    margin-top: $margin-sm;
    display: flex;
    flex-wrap: wrap;

    span {
      display: block;
      flex: 0 0 100%;
    }

    .ant-slider {
      flex: 1 1 auto;
    }
    .ant-input-number {
      flex: 0 0 auto;
      width: 5rem;
      margin-left: 0.65rem;
    }
  }

  .question-settings-item {
    margin-top: $margin-sm;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;

    .ant-radio-group {
      flex: 1 1 auto;
      display: flex;
      & > label {
        flex: 1 1 auto;
        text-align: center;
      }
    }
    &:nth-child(2) {
      span:first-child {
        flex: 0 0 auto;
        margin-right: $margin-md;
      }
    }
    &:nth-child(3) {
      & > span:first-child {
        flex: 1 1 100%;
        margin-bottom: $margin-xxs;
      }
      & > span:last-child {
        flex: 0 0 auto;
        margin-left: $margin-md;
      }
    }
    &:nth-child(4),
    &:nth-child(5) {
      .ant-select,
      button {
        margin-left: $margin-sm;
      }
      .ant-select {
        width: 4rem;
      }
    }
  }

  .question-ctrl {
    .ant-space {
      margin-top: $margin-xs;
    }
    .question-ctrl-notice {
      margin-top: $margin-sm;
    }
    & > span {
      display: block;
      margin-bottom: $margin-xxs;
    }
  }

  // styles on mobile
  @media screen and (max-width: 768px) {
    .question-types {
      margin-bottom: 0;
    }
    .question-settings {
      margin-top: 0;
      margin-bottom: 0;
    }
    .question-ctrl {
      margin-top: 0;
    }
  }
}
